"use client";

import Link from "next/link";
import { usePathname } from "next/navigation";
import { NAV } from "@/lib/nav";
import { ChevronDoubleLeftIcon } from "@heroicons/react/24/outline";

type Props = {
  collapsed: boolean;
  onToggleCollapsed: () => void;
  onNavigate?: () => void;
};

const FB_BLUE = "#1877F2";
const FB_BLUE_HOVER = "#166FE5";

export default function Sidebar({
  collapsed,
  onToggleCollapsed,
  onNavigate,
}: Props) {
  const pathname = usePathname();

  return (
    <aside
      className={[
        "h-full bg-white border-r border-blue-100",
        "transition-all duration-200 ease-out",
        collapsed ? "w-16" : "w-64",
      ].join(" ")}
    >
      {/* Brand */}
      <div className="h-14 px-4 flex items-center justify-between border-b border-blue-100">
        <Link
          href="/"
          onClick={onNavigate}
          className="flex items-center gap-3 min-w-0"
        >
          <div
            className="h-9 w-9 rounded-xl flex items-center justify-center text-white font-extrabold shadow-sm"
            style={{ backgroundColor: FB_BLUE }}
          >
            DV
          </div>

          {!collapsed && (
            <div className="min-w-0 leading-tight">
              <div className="text-sm font-extrabold text-slate-900 truncate">
                Doc Versioning
              </div>
              <div className="text-[11px] text-slate-500 truncate">
                Compare • Risk • Summary
              </div>
            </div>
          )}
        </Link>

        <button
          onClick={onToggleCollapsed}
          className="hidden md:inline-flex h-8 w-8 items-center justify-center rounded-lg border border-blue-100 hover:bg-blue-50"
          aria-label="Toggle sidebar"
        >
          <ChevronDoubleLeftIcon
            className={[
              "h-4 w-4 transition-transform",
              collapsed ? "rotate-180" : "",
            ].join(" ")}
            style={{ color: FB_BLUE }}
          />
        </button>
      </div>

      {/* Nav */}
      <nav className="px-2 py-3 space-y-4">
        {NAV.map((group) => (
          <div key={group.group}>
            {!collapsed && (
              <div className="px-3 mb-1 text-[11px] font-bold uppercase tracking-wide text-slate-400">
                {group.group}
              </div>
            )}

            <div className="space-y-1">
              {group.items
                // ❌ ตัด Versions / Settings ออกตรงนี้
                .filter(
                  (item) =>
                    !["/versions", "/settings"].includes(item.href)
                )
                .map((item) => {
                  const active =
                    pathname === item.href ||
                    (item.href !== "/" &&
                      pathname.startsWith(item.href));
                  const Icon = item.icon;

                  return (
                    <Link
                      key={item.href}
                      href={item.href}
                      onClick={onNavigate}
                      title={collapsed ? item.label : undefined}
                      className={[
                        "group flex items-center gap-3",
                        "rounded-xl px-3 py-2.5",
                        "text-sm font-semibold transition-colors",
                        active
                          ? "text-white shadow-sm"
                          : "text-slate-700 hover:bg-blue-50",
                      ].join(" ")}
                      style={
                        active
                          ? { backgroundColor: FB_BLUE }
                          : undefined
                      }
                      onMouseEnter={(e) => {
                        if (active)
                          e.currentTarget.style.backgroundColor =
                            FB_BLUE_HOVER;
                      }}
                      onMouseLeave={(e) => {
                        if (active)
                          e.currentTarget.style.backgroundColor =
                            FB_BLUE;
                      }}
                    >
                      <Icon
                        className="h-5 w-5 shrink-0"
                        style={{
                          color: active ? "#ffffff" : "#64748b",
                        }}
                      />
                      {!collapsed && (
                        <span className="truncate">
                          {item.label}
                        </span>
                      )}
                    </Link>
                  );
                })}
            </div>
          </div>
        ))}
      </nav>

      {/* Footer hint */}
      {!collapsed && (
        <div className="mt-auto p-3">
          <div className="rounded-xl border border-blue-100 bg-blue-50 p-3 text-xs font-semibold text-slate-600">
            Tip: อัปโหลด PDF 2 เวอร์ชัน แล้วกด{" "}
            <span style={{ color: FB_BLUE }}>Compare</span>
          </div>
        </div>
      )}
    </aside>
  );
}
