"use client";

import React from "react";
import Link from "next/link";
import { usePathname } from "next/navigation";
import {
  Bars3Icon,
  ChevronDoubleLeftIcon,
  ChevronDoubleRightIcon,
  ClockIcon,
  DocumentMagnifyingGlassIcon,
  UserCircleIcon,
  BellIcon,
} from "@heroicons/react/24/outline";
import { motion } from "framer-motion";

type TopbarProps = {
  collapsed: boolean;
  onToggleSidebar: () => void;
  onOpenMobile: () => void;
};

const FB_BLUE = "#1877F2";
const FB_GRADIENT = "linear-gradient(135deg, #1877F2 0%, #166FE5 100%)";

function getTitle(pathname: string) {
  if (pathname === "/") return "Compare Documents";
  if (pathname === "/dashboard") return "Dashboard";
  if (pathname === "/history") return "History";
  if (pathname.startsWith("/compare/")) return "Compare Detail";
  if (pathname === "/documents") return "Documents";
  if (pathname.startsWith("/reports/generate")) return "Generate Report";
  return "Dashboard";
}

function getSubtitle(pathname: string) {
  if (pathname === "/") return "Upload and analyze document changes";
  if (pathname === "/dashboard") return "Overview of your document analysis";
  if (pathname === "/history") return "View previous comparisons";
  if (pathname.startsWith("/compare/")) return "Detailed comparison analysis";
  if (pathname === "/documents") return "Manage your documents";
  if (pathname.startsWith("/reports/generate")) return "Create professional reports";
  return "Document Versioning Platform";
}

export default function Topbar({
  collapsed,
  onToggleSidebar,
  onOpenMobile,
}: TopbarProps) {
  const pathname = usePathname();
  const title = getTitle(pathname);
  const subtitle = getSubtitle(pathname);

  return (
    <motion.header 
      initial={{ y: -100 }}
      animate={{ y: 0 }}
      transition={{ duration: 0.5, type: "spring" }}
      className="sticky top-0 z-40 bg-white/80 backdrop-blur-xl border-b border-gray-200/50 shadow-sm"
    >
      <div className="h-16 px-4 md:px-6 lg:px-8 flex items-center justify-between">
        {/* Left Section */}
        <div className="flex items-center gap-3">
          {/* Mobile Menu Button */}
          <button
            onClick={onOpenMobile}
            className="lg:hidden inline-flex h-10 w-10 items-center justify-center rounded-xl border border-gray-200 hover:bg-gray-50 transition-all duration-200 hover:scale-110 active:scale-95"
          >
            <Bars3Icon className="h-5 w-5 text-gray-700" />
          </button>

          {/* Desktop Toggle Button */}
          <button
            onClick={onToggleSidebar}
            className="hidden lg:inline-flex h-10 items-center gap-2 rounded-xl px-3 border border-gray-200 hover:bg-gray-50 transition-all duration-200 hover:scale-105 active:scale-95"
          >
            {collapsed ? (
              <>
                <ChevronDoubleRightIcon className="h-4 w-4 text-gray-600" />
                <span className="text-sm font-semibold text-gray-700">
                  Expand
                </span>
              </>
            ) : (
              <>
                <ChevronDoubleLeftIcon className="h-4 w-4 text-gray-600" />
                <span className="text-sm font-semibold text-gray-700">
                  Collapse
                </span>
              </>
            )}
          </button>

          {/* Page Title */}
          <div className="ml-1">
            <div className="flex items-center gap-2">
              <h1 className="text-lg md:text-xl font-bold text-gray-900">
                {title}
              </h1>
              {pathname === "/reports/generate" && (
                <motion.span
                  animate={{ scale: [1, 1.1, 1] }}
                  transition={{ repeat: Infinity, duration: 2 }}
                  className="inline-flex items-center gap-1 px-2 py-0.5 rounded-full bg-gradient-to-r from-emerald-100 to-emerald-50 text-emerald-700 text-xs font-semibold border border-emerald-200"
                >
                  <span className="h-1.5 w-1.5 rounded-full bg-emerald-500 animate-pulse"></span>
                  New Feature
                </motion.span>
              )}
            </div>
            <p className="text-sm text-gray-500 hidden md:block">
              {subtitle}
            </p>
          </div>
        </div>

        {/* Right Section */}
        <div className="flex items-center gap-2">
          {/* Notification Bell */}
          <button className="relative h-10 w-10 rounded-xl border border-gray-200 hover:bg-gray-50 flex items-center justify-center transition-all duration-200 hover:scale-110 active:scale-95">
            <BellIcon className="h-5 w-5 text-gray-600" />
            <span className="absolute -top-1 -right-1 h-3 w-3 rounded-full bg-gradient-to-r from-red-500 to-red-600 border-2 border-white"></span>
          </button>

          {/* User Profile */}
          <button className="h-10 px-3 rounded-xl border border-gray-200 hover:bg-gray-50 flex items-center gap-2 transition-all duration-200 hover:scale-105 active:scale-95">
            <UserCircleIcon className="h-6 w-6 text-gray-600" />
            <span className="text-sm font-medium text-gray-700 hidden md:inline">
              Admin
            </span>
          </button>

          {/* Main Actions */}
          {pathname !== "/" && (
            <motion.div
              whileHover={{ scale: 1.05 }}
              whileTap={{ scale: 0.95 }}
            >
              <Link
                href="/"
                className="inline-flex items-center gap-2 rounded-xl px-4 py-2.5 text-sm font-semibold text-white shadow-lg transition-all duration-200"
                style={{ background: FB_GRADIENT }}
              >
                <DocumentMagnifyingGlassIcon className="h-4 w-4" />
                <span className="hidden sm:inline">New Compare</span>
                <span className="sm:hidden">Compare</span>
              </Link>
            </motion.div>
          )}

          <motion.div
            whileHover={{ scale: 1.05 }}
            whileTap={{ scale: 0.95 }}
          >
            <Link
              href="/history"
              className="hidden sm:inline-flex items-center gap-2 rounded-xl border border-blue-200 bg-white px-4 py-2.5 text-sm font-semibold hover:bg-blue-50 transition-all duration-200"
              style={{ color: FB_BLUE }}
            >
              <ClockIcon className="h-4 w-4" />
              History
            </Link>
          </motion.div>
        </div>
      </div>

      {/* Progress Bar for Reports Page */}
      {pathname.startsWith("/reports/generate") && (
        <div className="h-1 w-full bg-gray-200/50 overflow-hidden">
          <motion.div
            className="h-full bg-gradient-to-r from-blue-500 to-emerald-500"
            initial={{ width: "0%" }}
            animate={{ width: "75%" }}
            transition={{ duration: 2, repeat: Infinity, ease: "easeInOut" }}
          />
        </div>
      )}
    </motion.header>
  );
}